*,
*:after,
*:before {
  box-sizing: border-box;
}

.ba-container,
.ba-container-center {
  margin-left: auto;
  margin-right: auto;
}

img {
  max-width: 100%;
  height: auto;
}

html {
  height: 100%;
}
body,
html {
  min-height: 100%;
}
/* Text align */

.ba-text-left {
  text-align: left;
}

.ba-text-right {
  text-align: right;
}

.ba-text-center {
  text-align: center;
}

/* Absolute centering */

.ba-relative {
  position: relative;
  z-index: 1;
}

/* Circle element */

.ba-circle {
  border-radius: 50%;
}

/* Flex grid */
.ba-row {
  display: flex;
  flex-wrap: wrap;
  //   margin-left: -30px;
}
.ba-row + .ba-row {
  margin-top: 30px;
}
.ba-row > [class*="ba-col-"] {
  padding-left: 30px;
}
.ba-row--collapse {
  margin-left: 0;
}
.ba-row--collapse + .ba-row--collapse {
  margin-top: 0;
}
.ba-row--collapse > [class*="ba-col-"] {
  padding-left: 0;
}

.ba-col-12 {
  width: 100%;
}
