}
.ba-col-8 {
  width: 66.6666%;
}
.ba-col-7 {
  width: 58.33%;
}
.ba-col-6 {
  width: 50%;
}
.ba-col-5 {
  width: 41.66%;
}
.ba-col-4 {
  width: 33.333%;
}
.ba-col-3 {
  width: 25%;
}
.ba-col-2 {
  width: 16.666%;
}
.ba-col-1 {
  width: 8.3333%;
}

.ba-col-offset-7 {
  margin-left: 58.33%;
}

.ba-col-offset-2 {
